import React, { HTMLProps } from 'react';

interface IBlockquoteProps extends HTMLProps<HTMLDivElement> {
  color: 'red' | 'blue';
}

const Blockquote: React.FC<IBlockquoteProps> = ({ color, ...rest }) => (
  <blockquote className={`blockquote__${color}`} {...rest} />
);

export default Blockquote;
