import React from 'react';
import { LiveProvider, LiveEditor, LiveError, LivePreview } from 'react-live';
import vsDark from 'prism-react-renderer/themes/vsDark';

const starterCode = `const Element = () => {
  return (
    <div>
      <p>Hello There!</p>
    </div>
  );
}`;

const transformFunction = (code: string) => `${code}
  render(Element);`;

const JSXTutorial: React.FC = () => (
  <div className="tutorial">
    <div className="tutorial__header">
      <h3 className="tutorial__heading">
        <span className="tutorial__blinker" aria-hidden />
        Tutorial - Play with JSX!
      </h3>
    </div>
    <LiveProvider
      code={starterCode}
      transformCode={transformFunction}
      language="jsx"
      theme={vsDark}
      noInline
    >
      <div className="tutorial__provider">
        <div className="tutorial__section">
          <p className="tutorial__subheading tutorial__subheading--left">
            Editor
          </p>
          <LiveEditor className="tutorial__editor" />
        </div>
        <div className="tutorial__section">
          <p className="tutorial__subheading">Preview</p>
          <LivePreview className="tutorial__preview" />
          <LiveError />
        </div>
      </div>
    </LiveProvider>
  </div>
);

export default JSXTutorial;
