import React, { HTMLProps } from 'react';
// import Highlight, { defaultProps } from 'prism-react-renderer';
import { LiveProvider, LiveEditor, LiveError, LivePreview } from 'react-live';

type ICodeTutorialProps = HTMLProps<HTMLDivElement>;

const CodeTutorial: React.FC<ICodeTutorialProps> = ({ children, ...rest }) => (
  <div style={{ marginTop: `40px` }} {...rest}>
    <LiveProvider code={children.toString()} language="jsx" noInline>
      <LivePreview />
      <LiveEditor />
      <LiveError />
    </LiveProvider>
  </div>
);

export default CodeTutorial;
